.mainLight-off{
    fill: red;
}
.mainLight-work{
	animation: color_change 1s infinite alternate;
}
.mainLight-on{
    fill: #38FF00;
}
@keyframes color_change {
	from { fill: blue; }
	to { fill: red; }
}
@-webkit-keyframes color_change {
	from { fill: blue; }
	to { fill: red; }
}
@-moz-keyframes color_change {
	from { fill: blue; }
	to { fill: red; }
}
.portsRouter{
position: absolute;
bottom: -15px;
width: 90px;
height: 15px;
}