.logon-container {
    width: 100%;
    height: 100vh;
    padding: 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #eaebf6;
}

.logon-container section.form {
    width: 100%;
    max-width: 350px;
    margin-right: 30px;

}

.logon-container section.form form {
    margin-top: 60px;
}

.logon-container section.form form h1 {
    font-size: 32px;
    margin-bottom: 32px;
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

form input {
    width: 100%;
    height: 60px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 0 24px;
    margin-top: 10px;
}

form textarea{
    width: 100%;
    min-height: 140px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 16px 24px;
    line-height: 24px;
    resize: vertical;
}

.button {
    width: 100%;
    height: 60px;
    background: #5b81c7;
    border: 0;
    border-radius: 8px;
    color: #fff;
    font-weight: 700;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: filter 0.2s;
}

.button:hover {
    filter: brightness(80%);
}

.back-link {
    display: flex;
    align-items: center;
    margin-top: 40px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.2s;
}

.back-link svg {
    margin-right: 8px;
}

.back-link:hover {
    opacity: 0.8;
}

.error{
    border: 2px solid rgb(177, 39, 81);
}

.error_information{
    color: rgb(177, 39, 81);
}